// src/app/features/visualization/components/panel/panel.component.scss

.panel {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: white;

  .panel-header {
    padding: 1.5rem 1.5rem 1rem;
    border-bottom: 1px solid #e2e8f0;

    h3 {
      margin: 0;
      font-size: 1.125rem;
      font-weight: 600;
      color: #1e293b;
    }
  }

  .panel-content {
    flex: 1;
    padding: 1.5rem;
    overflow-y: auto;
  }
}

.search-box {
  margin: 0 0 1rem 0;

  input {
    width: 100%;
    padding: 0.5rem 0.75rem;
    border: 1px solid #e2e8f0;
    border-radius: 6px;
    outline: none;
    font-size: 0.875rem;
  }
}
.container-info {
  margin-bottom: 1.5rem;

  h4 {
    margin: 0 0 0.75rem 0;
    font-size: 0.875rem;
    font-weight: 600;
    color: #374151;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }

  .info-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    border-bottom: 1px solid #f1f5f9;

    &:last-child {
      border-bottom: none;
    }

    .label {
      font-size: 0.8125rem;
      color: #64748b;
      font-weight: 500;
    }

    .value {
      font-size: 0.8125rem;
      color: #1e293b;
      font-weight: 600;
    }
  }
}

.items-list {
  h4 {
    margin: 0 0 0.75rem 0;
    font-size: 0.875rem;
    font-weight: 600;
    color: #374151;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }

  .item {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.75rem;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
    border: 1px solid transparent;

    &:hover {
      background: #f8fafc;
      border-color: #e2e8f0;
    }

    &.selected {
      background: #fef3e2;
      border-color: #f97316;
    }

    .item-color {
      width: 1rem;
      height: 1rem;
      border-radius: 3px;
      flex-shrink: 0;
      box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    }

    .item-info {
      flex: 1;

      .item-header {
        margin-bottom: 0.25rem;

        .item-title {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          gap: 0.25rem;

          .item-id {
            font-size: 0.875rem;
            font-weight: 600;
            color: #1e293b;
            background: #f1f5f9;
            padding: 0.125rem 0.375rem;
            border-radius: 4px;
            font-family: 'Courier New', monospace;
          }

          .item-type {
            font-size: 0.75rem;
            font-weight: 500;
            color: #059669;
          }
        }
      }

      .item-details {
        font-size: 0.75rem;
        color: #64748b;
        margin-bottom: 0.25rem;
      }

      .item-flags {
        display: flex;
        gap: 0.25rem;
        margin-bottom: 0.25rem;

        .flag {
          font-size: 0.625rem;
          font-weight: 600;
          padding: 0.125rem 0.25rem;
          border-radius: 3px;
          text-transform: uppercase;

          &.fragile {
            background: #fef2f2;
            color: #dc2626;
            border: 1px solid #fecaca;
          }

          &.non-stackable {
            background: #fffbeb;
            color: #d97706;
            border: 1px solid #fed7aa;
          }
        }
      }

      .item-destination {
        display: flex;
        flex-direction: column;
        gap: 0.125rem;

        small {
          font-size: 0.65rem;
          color: #6b7280;
          display: flex;
          align-items: center;
          gap: 0.25rem;
        }
      }
    }
  }
}

// ===== SCROLLBAR STYLING =====
.panel-content::-webkit-scrollbar {
  width: 4px;
}

.panel-content::-webkit-scrollbar-track {
  background: #f1f5f9;
}

.panel-content::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 2px;

  &:hover {
    background: #94a3b8;
  }
}
